.page-header {
  width: 100%;
  min-height: 650px;
  background-color: $color-dark-blue;
  background-image: url("/img/pictures/intro-background-desktop.jpg");
  @include background;
  background-size: cover;
  box-shadow: 0 4px 4px $color-black-muted;
  content: "";
}
.page-header__wrapper {
  min-height: 60px;
  padding-top: 30px;
  @include flex;
  flex-wrap: wrap;

  @media (max-width: $vp-768) {
    padding-top: 30px;
  }

  @media (max-width: $vp-576) {
    justify-content: center;
  }

  @media (max-width: $vp-320) {
    flex-direction: column;
    justify-content: space-between;
  }
}

.page-header button {
  position: relative;
  display: none;
  width: 13px;
  height: 13px;
  padding: 0;
}

.page-header svg {
  position: relative;
  width: 70px;
  height: 38px;
  fill: $color-default-white;

  @media (max-width: $vp-768) {
    width: 38px;
    height: 20px;
  }

  @media (max-width: $vp-320) {
    width: 42px;
    height: 22px;
  }
}

.page-header__menu-closed {
  @media (max-width: $vp-320) {
    flex-direction: row;
  }
}

.page-header__menu-closed nav {
  @media (max-width: $vp-320) {
    display: none;
  }
}

.page-header__menu-closed button {
  @media (max-width: $vp-320) {
    display: block;
    background-color: transparent;
    background-image: url("/img/svg/burger-button.svg");
    @include background;
  }
}

.page-header__menu-opened nav {
  position: absolute;
  display: block;
  z-index: 3;
  top: 0;
  right: 15px;
}

.page-header__menu-opened button{
  @media (max-width: $vp-320)  {
  position: absolute;
  z-index: 4;
  display: block;
  top: 20px;
  right: 20px;
  background-image: url("/img/svg/close-button.svg");
  @include background;
}
}

.page-header__menu-nojs {
  @media (max-width: $vp-320) {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.page-header__menu-nojs nav {
  @media (max-width: $vp-320) {
    display: block;
  }
}

.page-header__menu-nojs button {
  @media (max-width: $vp-320) {
    display: none;
  }
}

.page-header__menu-nojs svg,
.page-header__menu-opened svg {
  @media (max-width: $vp-320) {
  position: absolute;
  z-index: 4;
  top: 20px;
  left: 40px;
  fill: $color-dark-blue;
  }
}




